import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';
import linkState from 'linkstate';

import { route } from 'preact-router';

import mu from '../../lib/musher-util';

const MARGIN_TOP_BACK = mu.marginTopBack(false);

const initialState = {
	searchText: '',
    showInfo: {},
};

@observer
class Settings extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadTeams() {
		this.setState({ isLoading: true });
		const { teamStore, userStore, messageStore } = this.props.stores;
		await teamStore.load(undefined, false, { addData: ['workoutSummary', 'musherSummary'] });
		await userStore.getNotifications();
        await messageStore.getUnReadMessages();
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadTeams();
	}

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

	render() {
		const { teamStore } = this.props.stores;
        const { teams } = teamStore;
		return (
			<div class='container-fluid' style='margin-bottom: 200px; margin-top: 60px;'>
                <button class='btn btn-link pl-0 fixed-top text-primary'
                    style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                    onClick={this.back}>
                    <i class='fas fa-arrow-circle-left' />
                </button>

				<div class='row m-2'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h1><Text id='settings.team'>Team</Text></h1>
                    </div>
                </div>
				<div class='row m-2'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3 bg-light rounded-lg' style='font-size: 1.2em;'>
                        {teams && teams.map((team, idx) => {
                            const image = team.image && team.image.s3SmallLink ? team.image : null;
                            return (
                                <div class={`row ${idx > 0 ? 'border-top' : ''}`}>
                                    <div class='col-12 py-2 d-flex align-items-center position-relative'>
                                        <div class={`text-center border rounded-circle imageRoundedThumb mr-3`} style={image ? `background-image: url("${image.s3SmallLink}"); background-size: cover;` : 'line-height: 1.0em;'}>
                                            {!image && <i class='fas fa-users text-muted mt-0' style='font-size: 13px;' />}
                                        </div>
                                        <span class='flex-grow-1' style='line-height: 1.0em;'>
                                            <a href={`/settings/team/${team.id}`} class='stretched-link'>{team.name}</a>
                                        </span>
                                        <button class='btn btn-link btn-lg py-0 px-1 mr-3'>
                                            <i class='fas fa-angle-right' />
                                        </button>
                                    </div>
                                </div>
                            );
                        })}
					</div>
				</div>
			</div>
		);
	}
}

export default Settings;
